.windows__windowmanager {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.windows__windowmanager .windows__windowmanager__screenarea {
    position: relative;
    flex: 1;
}

.windows__windowmanager .windows__windowmanager__topnavbars, .windows__windowmanager .windows__windowmanager__bottomnavbars {
    z-index: 99999;
}
