#maximizeicon {
    fill-rule:evenodd;
    clip-rule:evenodd;
    stroke-linecap:square;
    stroke-miterlimit:1.5;
}

#maximizeicon rect {
    stroke: none;
}

#maximizeicon #icon {
    fill:none;
    stroke:black;
    stroke-width:1px;
}

#maximizeicon.maximizeicon--disabled #icon {
    stroke:#707070;
}

#maximizeicon #background {
    fill:white;
    transition: fill 0.125s;
}

#maximizeicon:hover #background {
    fill:#E5E5E5;
}

#maximizeicon.maximizeicon--disabled:hover #background {
    fill:white;
}