.step10__drag {
    position:relative;
    min-height:40vh;
}

.step10__dragelement {
    position:absolute;
    top:33%;
    left:20%;
    background:#00c000;
    height:5vh;
    width:5vh;
    border-radius:2.5vh;
    transition: top 0.1s, left 0.1s;
    z-index:1;
}

.step10__dragelement.step10__dragelement--dropped {
    left:auto;
    right:20%;
}

.step10__dragtarget {
    position:absolute;
    top:33%;
    right:20%;
    background:#aaa;
    height:5vh;
    width:5vh;
    border-radius:2.5vh;
}
