#minimizeicon {
    fill-rule:evenodd;
    clip-rule:evenodd;
    stroke-miterlimit:1.5;
}

#minimizeicon #background {
    fill:white;
    transition: fill 0.125s;
}
#minimizeicon #background:hover{
    fill:#E5E5E5;
}

#minimizeicon.minimizeicon--disabled #background:hover{
    fill: #fff;
}

#minimizeicon #minus {
    fill:none;
    stroke:black;
    stroke-width:1.33px;
}

#minimizeicon.minimizeicon--disabled #minus {
    stroke: #707070;
}