.windows__titlebar {
    color:#999999;
    text-align:left;
    display:flex;
    flex-direction: row;
}

.windows__titlebar--active {
    color:#000;
}

.windows__titlebar__mainicon {
    width:1rem;
    height:1rem;
    padding-top:0.4rem;
    padding-left:0.5rem;
    padding-right:0.125rem;
}

.windows__titlebar__title {
    text-overflow: ellipsis;
    overflow: hidden;
    padding-top:0.125rem;
    padding-bottom:0.125rem;
    flex: 1;
    height:1.5rem;
    line-height:1.5rem;
}

.windows__titlebar__icons {
    display:flex;
    flex-direction: row;
    opacity:0.8;
}

.windows__titlebar--active .windows__titlebar__icons {
    opacity:1;
}

.windows__titlebar__icon {
    height:1.75rem;
}

.windows__titlebar__icon--close {
    border-top-right-radius: 1px;
}