#closeicon {
    fill-rule:evenodd;
    clip-rule:evenodd;
    stroke-linecap:round;
    stroke-linejoin:round;
    stroke-miterlimit:1.5;
}

#closeicon #background {
    fill: white;
    transition: fill 0.125s;
}


#closeicon.closeicon--disabled:hover #background {
    fill: white;
}

#closeicon:hover #background {
    fill:#E81123;
}

#closeicon #x path {
    fill:none;
    stroke:black;
    stroke-width:1px;
    transition: stroke 0.125s, stroke-width 0.125s;
}
#closeicon:hover #x path {
    stroke:white;
    stroke-width:2px;
}
#closeicon.closeicon--disabled #x path,
#closeicon.closeicon--disabled:hover #x path {
    stroke:#707070;
    stroke-width:1px;
}