.windows__window {
    position: absolute;
    background-color: #fff;
    transition: all 0.125s;
    border-radius:1px;
    font-size:0.75rem;
    line-height:1.25rem;
    cursor: default;
    border: 1px solid rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
    box-shadow: 2px 2px 10px 0 rgba(0,0,0,0.2);
}

.windows__window--active {
    border: 1px solid rgba(0,0,0,0.75);
    box-shadow: 2px 2px 10px 0 rgba(0,0,0,0.40);
}

.windows__window--minimized {
    top:100%;
    left:50%;
    width:10%;
    height:0;
}

.windows__window--maximized {
    top:0;
    left:0;
    width:100%;
    height:100%;
}

.windows__window {
    flex: 1;
}

.windows__window__contents{
    flex:1;
    position:relative;
    display: flex;
    flex-direction: column;
}

.windows__window__resize {
    position:absolute;
}

.windows__window__resize--top {
    top:-5px;
    left:5px;
    right:5px;
    height:10px;
    cursor: n-resize;
}

.windows__window__resize--left {
    left:-5px;
    top:5px;
    bottom:5px;
    width:10px;
    cursor: w-resize;
}

.windows__window__resize--bottom {
    bottom:-5px;
    left:5px;
    right:5px;
    height:10px;
    cursor: s-resize;
}

.windows__window__resize--right {
    right:-5px;
    top:5px;
    bottom:5px;
    width:10px;
    cursor: e-resize;
}


.windows__window__resize--topleft {
    top:-5px;
    left:-5px;
    height:10px;
    width:10px;
    cursor: nw-resize;
}


.windows__window__resize--topright {
    top:-5px;
    right:-5px;
    height:10px;
    width:10px;
    cursor: ne-resize;
}

.windows__window__resize--bottomleft {
    bottom:-5px;
    left:-5px;
    height:10px;
    width:10px;
    cursor: sw-resize;
}


.windows__window__resize--bottomright {
    bottom:-5px;
    right:-5px;
    height:10px;
    width:10px;
    cursor: se-resize;
}