#restoreicon {
    fill-rule:evenodd;
    clip-rule:evenodd;
    stroke-linecap:square;
    stroke-miterlimit:1.5;
}

#restoreicon rect {
    stroke: none;
}
#restoreicon #background {
    fill:white;
    transition: fill 0.125s;
}
#restoreicon:hover #background{
    fill:#E5E5E5;
}

#restoreicon.restoreicon--disabled:hover #background{
    fill:#fff;
}

#restoreicon #x1 {
    fill:none;stroke:black;stroke-width:1.33px;
}

#restoreicon #x2 {
    fill:white;stroke:black;stroke-width:1.33px;
}

#restoreicon.restoreicon--disabled #x1, #restoreicon.restoreicon--disabled #x2 {
    stroke:#707070;
}